/**
 * Base SCSS
 */
nav {
	padding: 1rem 0;

	a {
		color: #FFF;
		@extend .sub;
		margin-left: 2rem;
	}

}

#masthead {
	background-color: $noir;
	background: url(assets/img/tech-pattern-masthead.svg), #1A1921;
	width: 100%;
	height: 90vh;
	margin: 0;
	color: #FFF;
	padding: 10rem 0;
	display: flex;
	align-items: flex-end;
	position: relative;

	@media screen and (max-width:1199px) {
		height: 900px;
	}

	@media screen and (max-width:991px) {
		padding: 5rem 0;
	}

	h1,
	p,
	a {
		color: #FFF;
	}

	.wave {
		position: absolute;
		left: 0;
		right: 0;
	}

	.programmer-boy {
		margin: 0 auto;
	}

	.icons a {
		height: 28px;
		width: 28px;
		display: inline-block;
		margin-left: 1rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	.content {
		position: relative;
		padding: 3rem;
		background: rgba(16, 15, 28, 0.9);
		border: 1px solid rgba(76, 117, 242, 0.1);
		box-sizing: border-box;
		border-radius: 8px;
	}

	h1 {
		text-transform: uppercase;
		font-size: 1.5rem;
		letter-spacing: 2px;
		margin: 0 0 1rem;
		padding: 0;
	}

	.skills {
		position: relative;
		padding: 0 0 0 3rem;

		@media screen and (max-width:991px) {
			padding: 2rem 0 0 0;
		}
	}

	.VerticalLine {
		border-left: 1px solid $blue;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}

	.TopSkill {
		color: #FFF;
		text-transform: uppercase;
		font-size: 1.5rem;
		letter-spacing: 2px;
		margin-bottom: 1rem;
	}

	ul.Skills {
		font-weight: bold;
		letter-spacing: 2px;
		text-transform: uppercase;
		list-style-type: none;

		@media screen and (max-width:991px) {
			display: flex;
			flex-wrap: wrap;
			max-width: 450px;

			li {
				margin-right: 1rem;
			}
		}

		li {
			font-size: 0.9rem;
			margin-bottom: 0.25rem;
		}
	}

	p {
		@media screen and (min-width:1500px) {
			padding-right: 5rem;
		}
	}
}










#ProjectWork {
	background-color: white;
	background: white;
	position: relative;
	width: 100%;
	height: 900px;
	margin: 0;
	color: #FFF;
	padding: 5rem 0;
  h2{
    margin-bottom:3rem;
    color:$blue;
    text-transform: uppercase;
    letter-spacing: 3px;
  }

	.RectangleProject {
		border: 1px solid #4C75F2;
		border-radius: 8px;
    display:flex;
    align-items: center;
    justify-content: space-between;
    height:400px;
    @media screen and (max-width:1199px){
      .image{
        width:400px;
      }
      .content{
        width:calc(100% - 400px);
      }
    }
    @media screen and (max-width:991px){
      flex-direction: column;
    }
    .image{
      width:500px;
      height:100%;
      background-color: $blue;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        height:80%;
        width:80%;
        object-fit: contain;
      }
    }
    .content{
      padding:3rem;
      width:calc(100% - 500px);
      
    }
	}

	.ProjectTitleName {
		font-weight: 600;
		font-size: 24px;
		line-height: 30px;
		letter-spacing: 3px;
		text-transform: uppercase;
		color: $dark-text;
	}

	.ProjectSubTitleName {
		font-size: 14px;
    margin-bottom:1rem;
    @extend .sub;
	}
  .tags{
    margin-bottom:1rem;
    span{
      @extend .btn;
      @extend .btn.outline;
      &:hover{
        color:$blue;
      }
    }
  }
	.ProjectDescription {
		color: #3F3D56;
    margin-bottom:1rem;
    max-width:600px;
	}
}











#lightcase-case{
  font-family:'Source Sans Pro', Arial, Helvetica, sans-serif;
}

//https://www.w3schools.com/howto/tryit.asp?filename=tryhow_css_modal
.lightcase-contentInner {
	display: flex;
	background: #1A1921;
  padding:2rem;
  color:#FFF;
  overflow-x:hidden;
  overflow-y:scroll;
  h2{
    color:#FFF !important;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 2px;
    text-transform: uppercase;
  }
  .tech{
    margin-top:1rem;
  }
  h3.popout-Technology {
    @extend .sub;
    color:#FFF !important;
    font-size:1rem;
  }
  .btn.white{
    margin-left:0.5rem;
  }
}

/* Modal Content */
.popout-description {
	background-color: #fefefe;
	margin: auto;
	padding: 20px;
	border: 1px solid #888;
	width: 80%;
}


.popout-Subtitle {
  @extend .sub;
	color: #4C75F2;
}


.popout-Technology-button {

}

h3.Heading {
  color:#FFF !important;
  margin-top:2rem;
}

p.Content {
	color: rgba(255, 255, 255, 0.8) !important;
  font-size:1rem;
}


/* Slideshow container */
.slideshow {
	position: relative;
	margin: 2rem auto 0;
}

/* Hide the images by default */
.mySlides {
	display: none;
  justify-content: center;
  flex-direction: column;
}

/* Next & previous buttons */
.prev,
.next {
	cursor: pointer;
	position: absolute;
	top: 0;
  bottom:50%;
	width: 100px;
	color: white;
  height:100%;
  display:flex;
  align-items: center;
  justify-content:center;
  svg path{
    fill:#FFF;
    transition: 250ms all ease;
  }
  &:hover{
    svg path{
      fill:$blue;
    }
  }
  
}

.prev{
  left:-50px;
}

/* Position the "next button" to the right */
.next {
	right: -50px;
}

.project-img {
	position: relative;
	width: calc(100% - 100px);
  margin:0 auto;
	height: 500px;
  object-fit:cover;
  @media screen and (max-width:1199px) {
    height:400px;
  }
}

/* Caption text */
.text {
	color: #f2f2f2;
	font-size: 0.9rem;
	padding: 1rem;
	position: relative;
	width: 100%;
	text-align: center;
  font-style: italic;
}

/* Number text (1/3 etc) */
.numbertext {
	color: #f2f2f2;
	font-size: 12px;
	padding: 8px 12px;
	position: absolute;
	top: 0;
}

/* The dots/bullets/indicators */
.dots{
  margin:0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dot {
	position: relative;
	cursor: pointer;
	height: 8px;
	width: 8px;
	margin: 0 0.2rem;
	background-color: #bbb;
	border-radius: 50%;
	display: inline-block;
	transition: background-color 0.6s ease;
}

.active,
.dot:hover {
	background-color: #4C75F2;
}

/* Fading animation */
.fade {
	-webkit-animation-name: fade;
	-webkit-animation-duration: 1.5s;
	animation-name: fade;
	animation-duration: 1.5s;
}

@-webkit-keyframes fade {
	from {
		opacity: .4
	}

	to {
		opacity: 1
	}
}

@keyframes fade {
	from {
		opacity: .4
	}

	to {
		opacity: 1
	}
}



#Contact {
	display: flex;
	background-color: white;
	background: url(assets/img/contact-pattern.svg), #4C75F2;
	background-size: contain;
	width: 100%;
	color: #FFF;
	padding: 10rem 0;

	h1,
	h2,
	p,
	a {
		position: relative;
		color: white;
	}
}

.contact-message {
	position: relative;
	font-weight: 600;

	line-height: 20px;
	letter-spacing: 2px;
	text-transform: uppercase;


}

.contact-rectangle {

	position: relative;
	width: 650px;
	height: 70px;
	background: rgba(26, 25, 33, 0.9);
	backdrop-filter: blur(20px);

	/* Note: backdrop-filter has minimal browser support */
	border-radius: 8px;
}

.contact-rectangle-name {

	position: relative;




	font-weight: 600;
	font-size: 14px;
	line-height: 18px;

	/* identical to box height */
	display: flex;
	align-items: center;
	letter-spacing: 2px;
	text-transform: uppercase;

	color: #4C75F2;
}
